import { useState, useContext, useEffect } from "react";
import { useParams } from "react-router-dom";
import { Context } from "../../index";
import { Image, Header, Popup, Segment, Button, Container, Icon, Message } from "semantic-ui-react";
import { REACT_APP_API_STATIC, REACT_APP_MAIN_URL } from "../../utils/consts";
import { CopyToClipboard } from "react-copy-to-clipboard";
import UnSupport from "../../components/UnSupport";
import NotFound from "../../pages/NotFound/NotFound";
import AppLoading from "../../components/AppLoading";
import ImageGallery from "react-image-gallery";
import "./AppPage.css";
import "../../../node_modules/react-image-gallery/styles/css/image-gallery.css";

let AppPage = ()=>{
    let {user, app} = useContext(Context);
    let {id} = useParams();
    let [appInfo, setAppInfo] = useState(null);
    let [pageLoading, setPageLoading] = useState(true);
    let [installLoading, setInstallLoading] = useState(false);
    let [pageWidth, setPageWidth] = useState(window.innerWidth);
    let [copy, setCopy] = useState({value : "", status : false});
    let [message, setMessage] = useState({});
    const pageLink = window.location.href;
    
    useEffect(()=>{
        let query = new URLSearchParams(window.location.search),
            status = query.get('status'),
            localApp;

        

        app._appList.forEach(item =>{
            let itemIndex = item.apps.findIndex(x=>x.app_id === id);
            if(itemIndex !== -1) {
                localApp = item.apps[itemIndex];
                if(localApp.screenShots && parseInt(localApp.screenShots) >0){
                    let screens = [];
                    for(let i = 1;i<=parseInt(localApp.screenShots);i++){
                        screens.push({
                            original : `${REACT_APP_MAIN_URL}/${REACT_APP_API_STATIC}app/${localApp.app_id}/screen${i}.png`,
                            thumbnail : `${REACT_APP_MAIN_URL}/${REACT_APP_API_STATIC}app/${localApp.app_id}/screen${i}.png`,
                            originalHeight : '390px'
                        });
                    }
                    localApp.screenShots = screens;
                }
                setAppInfo(localApp);
            }
        });

        if(localApp){
            if(status === "'redirect'" && user._appInstalled) window.location.replace(localApp.app_link);
            else {
                document.title = `PwaBox - ${localApp.app_name}`;
                window.addEventListener('resize',()=>{
                    setPageWidth(window.innerWidth);
                });
                setCopy({value : pageLink, status : false});
                if(!user._appInstallSupport) {
                    messageHandler('info','Нет поддержки!', <UnSupport/>, true)
                } else {
                    //TESTING!!!!!!!!
                    user.regManifest(`${REACT_APP_MAIN_URL}/${REACT_APP_API_STATIC}app/${localApp.app_id}/site.webmanifest`);
                    window.onappinstalled = ()=>{
                        console.log('111');
                    }
                    window.addEventListener('appinstalled', (evt) => {
                        console.log('a2hs installed');
                    });
                    navigator.getInstalledRelatedApps().then(relatedApps=>{
                        relatedApps.forEach((app) => {
                            console.log(app.id, app.platform, app.url);
                        });
                    });
                    //TESTING!!!!!!!!
                }
            }
        }
        setPageLoading(false);
    },[app, id, pageLink, user]);

    let messageHandler = (type, header, message, critical)=>{
        setMessage({type, header, message, critical});
    }

    let installHendler = async ()=>{
        try{
            setInstallLoading(true);
            if(typeof user._appEvent.prompt === 'function'){
                user._appEvent.prompt();
                let { outcome } = await user._appEvent.userChoice, prewWin;
                if(outcome === 'accepted'){
                    prewWin = window;
                    window.location.open(`${pageLink}?status='redirect'`);
                    prewWin.open(pageLink,'_blank');
                }
            } else messageHandler('negative', 'Произошла ошибка!', 'При установке произошла ошибка, повтори попытку через некоторое время.', true);
            setInstallLoading(false);
        } catch (e) {
            messageHandler('negative', 'Произошла ошибка!', 'При установке произошла ошибка, повтори попытку через некоторое время.', true);
        }
    }

    let visitSite = ()=>{
        return window.open(appInfo.app_link, '_blank');
    }
    
    return (
        <Segment className="appPageSegment">
            {pageLoading && <AppLoading/>}
            {!pageLoading &&
                <>
                    {!appInfo && <NotFound type="app"/>}
                    {appInfo && 
                        <div
                            className="appPageInnerBlock"
                        >
                            <Header as="h1">
                                {appInfo.app_name}
                            </Header>
                            <div style={{color : 'rgba(0,0,0,.4)', fontSize:'1em', margin : '-12.5px 0 15px'}}>{appInfo.category_name}</div>
                            <Image
                                size="small"
                                src={`${REACT_APP_MAIN_URL}/${REACT_APP_API_STATIC}app/${appInfo.app_id}/logo.png`}
                                className="appMainImage"
                            />
                            {message.header &&
                                <Message 
                                    negative={message.type === 'negative'}
                                    className="messageBlock"
                                >
                                    <Message.Header>{message.header}</Message.Header>
                                    <p>{message.message}</p>
                                </Message>
                            }
                            <div
                                className="buttonBlock"
                            >
                                <Button
                                    disabled={message.critical || installLoading || !user._appInstallSupport}
                                    className="installButton"
                                    onClick={installHendler}
                                    loading={installLoading}
                                    primary
                                >
                                    <Icon name='arrow alternate circle down outline'/>
                                    {pageWidth > 767 &&<>Установить</>}
                                </Button>
                                <Popup content='Перейти на сайт приложения' trigger={<Button
                                        className="secondaryPageButton"
                                        icon="external alternate"
                                        onClick={visitSite}
                                    />}
                                />
                                <Popup content='Копировать ссылку на приложение' trigger={
                                    <CopyToClipboard
                                        text={copy.value}
                                        onCopy={() => setCopy({status : true})}
                                    >
                                        <Button
                                            className="secondaryPageButton"
                                            icon={copy.status ? "checkmark" : "copy"}
                                            disabled={copy.status}
                                        />
                                    </CopyToClipboard>
                                } />
                            </div>
                            <Container
                                className="descriptionBlock"
                            >
                                <p>{appInfo.app_description}</p>
                            </Container>
                            {appInfo.screenShots &&
                                <div
                                    style={{marginTop : '15px'}}
                                >
                                    <ImageGallery
                                        lazyLoad={true}
                                        showThumbnails={false}
                                        items={appInfo.screenShots}
                                        showFullscreenButton={false}
                                        showPlayButton={false}
                                        showBullets={true}
                                        showNav={false}
                                        autoPlay={true}
                                    />
                                </div>
                            }
                        </div>
                    }
                </>
            }
        </Segment>
    )
}

export default AppPage;